* {
  box-sizing: border-box;
}

body {
  height: 100vh;
  background-color: #f0f0f0;
  margin: 0;
  padding: 16px;
  font-family: Poppins, sans-serif;
}

#maxNumber, #minNumber, #average, #median, #increasingSequence, #decreasingSequence {
  width: 600px;
  box-sizing: border-box;
  border-radius: 5px;
  padding: 10px;
}

input[type="file"] {
  cursor: pointer;
  border-radius: 5px;
  padding: 5px;
  font-family: Poppins, sans-serif;
  font-size: 16px;
  transition: background-color .3s;
}

input[type="file"]:hover {
  color: #fff;
  background-color: #82acd8;
}

